.boxInfo {
  /* float: right; */
  display: flex;
  flex-direction: column;
  font-size: 20px;
  width: 20vw;
  margin-top: 70px;
  /* border: 1px solid red; */
  /* border: 1px solid blue; */
  /* font-family: 'Krub', sans-serif; */
  color: rgb(126, 25, 25);
  /* font-family: 'Mali', cursive; */
  font-size: normal;
  text-align: center;
  /* position: relative; */
  border-radius: 30px;
  position: absolute;
  right: 20%;

  -webkit-transform: translate(100%, 0);

          transform: translate(100%, 0);

  transition: 3s translate;
  
}
.boxInfo::after {
  content: '';
  display: block;
  opacity: 0.4;
  background-color: rgb(224,224,224); 
  width: 20vw;
  height: 100%;
  position: absolute;
  z-index: 1;
  border-radius: 50px;
 
  
  
  
}
.boxContent {
  display: absolute;
  z-index: 2;
  padding: 40px 10px;
}
.boxContent h2 {
  text-align: left;
  z-index: 2;
  font-weight: normal;
  font-size: 25px;
}

.boxContent h2 span {
  font-weight: bold;
}
.boxContent h1 {
  
  z-index: 2;
}
.boxContent img {
  width: 15vw;
  height: auto;
  /* border: 1px solid green; */
  margin: 50px 0 50px 30px;
  z-index: 2;
}

.boxContent button {
  padding: 10px 20px;
  background-color: rgb(187,135,97);
  color: white;
  font-family: 'Mali', cursive;
  font-weight: bold;
  font-size: 30px;
  border-radius: 50px;
  text-decoration: none;
  position: relative;
  z-index: 100;
  margin-top: 30px;

  
}
