* {
    margin: 0;
    padding: 0;
    border: 0;
    line-height: 1.4em;
    vertical-align: baseline;
    text-decoration: none;
    outline: 0;
    }

.heading {
    position: relative;
    padding-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* max-width:100%; */
}

.heading h1 {
    position: absolute;
    text-align: center;
    font-family: 'Krub', sans-serif;
    color: white;
    /* margin-left: 700px; */
    font-size: 60px;
    z-index: 2; 
    padding-top: 100px;
    /* text-transform: uppercase; */
    
}

.heading h1 span {
    font-size: 70px;
    color: rgb(126, 25, 25);
}

.heading h1::after {
    height: 4px;
    width: 700px;
    content: "";
    background: white;
    display: block;
    position: absolute;
    left: -50%;
    -webkit-transform: translate(350px);
            transform: translate(350px);
    bottom: -20px; 
}

.heading h2 {
    position: absolute;
    text-align: center;
    font-family: 'Krub', sans-serif;
    color: white;
    /* margin-left: 700px; */
    font-size: 50px;
    z-index: 2; 
    padding-top: 250px;


}
.heading a {
    padding: 10px 20px;
    background-color: rgb(187,135,97);
    color: white;
    font-family: 'Mali', cursive;
    font-weight: bold;
    font-size: 20px;
    border-radius: 50px;
    text-decoration: none;
    
    margin-top: 20px;
    position: absolute;
    right: 10px;
    z-index: 2; 

}

.banner-sliders {
    /* flex-grow: 1; */
    /* position: relative; */
    /* display: flex;
    justify-content: center; */
    width: 100vw;
    max-width:100%;
    height: 700px;
}

.banner-slide {
    position: absolute;
    
    height: 95%;
    width: 100vw;
    max-width:100%;
    display: block;
    background-size: cover;
    opacity: 0;
    transition: opacity 5s; 
    
}

.banner-slide-active {
    opacity: 1;
    z-index: 1; 
    
}

.PostContainer {

    display: flex;
    justify-content: center;
}

.post {
    display: block;
    width: 60%;
